.product_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid mediumaquamarine;
    padding: 10px;
}

.product-img {
    width: 300px;
    height: auto;
}

.product-title {
    margin-top: 15px;
    font-size: 32px;
    font-weight: 600;
    text-decoration: underline;
}

.product-details {
    margin-top: 10px;
    line-height: 1.3;
}

.product-details::first-letter {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 32px;
}