.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.nice-button {
  background-color: pink;
  padding: 20px;
  border-radius: 2px;
  text-decoration: none;
  color: white;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 20px;
  display: inline-block;
}

.description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.assemble {
  display: flex;
  width: 100vw;
  overflow-x: scroll;
}

.assembled-product{
  display: flex;
}

.assembled-product-vertical{
  display: flex;
  flex-direction: column;
}

.assembled-label {
  margin: 10px;
}

.color-card {
  padding: 20px;
  background-color: black;
  color: white;
  border-radius: 15px;
  margin: 10px;
  width: 100px;
}

.dragdrop {
  display: flex;
}

.dragg {
  height: 100vh;
  overflow-y: scroll;
  display: flex;
}

ul {
  list-style: none;
}


.box-col {
  height: 100vh;
  width: 100vw;
  background-color: grey;
  color: white;
}